@import 'animate.css';
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
body {
    margin: 0;
    min-height: 100%;
}
@import 'loaders.css/src/animations/ball-rotate.scss';


.loader-active {
  display: block;
  position: absolute;
  left: 0;
  right: 0;
  margin: auto;
  bottom:0;
  top:0;
  width: 50px;
  height: 50px;
  animation: fadeOut 1s 1s;
  animation-fill-mode: forwards;
  
}
html {
  scroll-behavior: smooth;
}
.container {
  width: 85%;
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
}