.profile-box {
    height:60vh;
    width:55vw;
    margin:auto;
    border-style:solid;
    border-radius:25px;
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    animation: fadeIn 4s;
  
    background-color:#1b2430;
    content: ' ';
    opacity: 0.98;
  
 

  
}
.profile-background {
    height:80vh;
    width:100%;
    background-image: url('../../assets/images/aboutus.png');
    background-size:cover;
}
.profile-avatar {
    text-align: center;
    height:25vh;
    width:25vw;
    margin:auto;
   
    

}

.info-box {
    border-style:solid;
    margin:auto;
    position:absolute;
    left:0;
    right:0;
    height:25vh;
    width:50vw;
    top:25vh;
    border-width:thin;
    
    box-shadow:
    2.8px 2.8px 2.2px rgba(0, 0, 0, 0.02),
    6.7px 6.7px 5.3px rgba(0, 0, 0, 0.028),
    12.5px 12.5px 10px rgba(0, 0, 0, 0.035),
    22.3px 22.3px 17.9px rgba(0, 0, 0, 0.042),
    41.8px 41.8px 33.4px rgba(0, 0, 0, 0.05),
    100px 100px 80px rgba(0, 0, 0, 0.07)
  ;

    font-weight:lighter;
    text-indent:25px;
    background-color: white;
    

}
.basic-h3 {
    text-align: center;
}
.greeting {
    font-style: italic;
    color:white;
    position:absolute;
    top:5vh;
    
}
.profile-image{
    border-radius: 50%;
    height:200px;
    width:200px;
}
.uploadh2 {
    color:white;
    position:absolute;
    top:15vh;
    text-align: center;
}