nav {
    display:flex;
    align-items:center;
    justify-content: space-between;
    background: #1b2430;
    padding: 20px 80px;
    box-shadow: 0 5px 15px rgba(0,0,0, 0.06);
    height:10vh;
    position:sticky;
    top: 0;
    z-index: 999;
    
    a.active {
        svg{
            color: #0074B7;
        }
} }
#navbar{
    display:flex;
    align-items: center;
    justify-content:center;
    border-radius:15px;
}
#navbar li{
    list-style:none;
    padding: 0 20px;
    position: relative;
}
#navbar li a{
    text-decoration:none;
    font-size: 1.3rem;
    font-weight: 600;
    color: white;
    transition: 0.3s ease-in-out;
}
#navbar li a:hover{
    color: #0074B7;
}
#navbar li a:hover::after,
#navbar li a:active::after {
    content:'';
    width:80%;
    height: 2px;
    background: #0074B7;
    position: absolute;
    bottom: -4px;
    left: 20px;
}
#mobile i {
    color:#fff;
    align-items: center;
}
#mobile {
    display:none;
}
@media screen and (max-width:1180px){
    #navbar{
        display:flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content:flex-start;
        position: fixed;
        top: 90px;
        right: -300px;
        width:300px;
        height:100vh;
        background: #2a3239;
        box-shadow: 0 40px 60px rgba(0,0,0,0).01;
        padding: 40px 0 0 10px;
        transition: 0.3s ease-in-out;
       
       
    }

    #navbar.active {
        left:0px;
    }
    #navbar li{
        margin-bottom: 25px;
    }
    #mobile {
        display:block;
    }
    #mobile i{
        font-size:24px;
        cursor: pointer;
    }
}
